var mnu = 1;

document.querySelector(".nav_btn").addEventListener("click", mob_nav);
var close_nav = document.querySelectorAll(".js_close_nav");

close_nav[0].addEventListener("click", mob_nav);
close_nav[1].addEventListener("click", mob_nav);

function mob_nav(){
  if(mnu == 1){
    document.querySelector("#nav_mobile").setAttribute("class", "nav_mobile-visible");
    document.querySelector("#menu-toggle").setAttribute("class", "open");
    mnu = 0;
  }
  else if(mnu == 0){
    document.querySelector("#nav_mobile").setAttribute("class", "nav_mobile-hidden");
    document.querySelector("#menu-toggle").setAttribute("class", "");
    mnu = 1;
  }
}



document.querySelector(".notify > a").addEventListener("click", function(){
  this.style.opacity = "0";
  document.querySelector(".notify form").setAttribute("class", "f-visible");
})